*{
    font-family: Poppins;
}
body{
    margin: 0px;
    background-image: url("./images/image9.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}
@media only screen and (min-width: 360px)
{
    .nav-container{
        height: 4rem;
        box-shadow: 0px 3px 7px rgba(0,0,0,0.1);
        display:flex;
        justify-content: center;
        width: 100%;
        background: white;
    }
    .hero-images{
        display:none;
    }
    .hero-text{
        text-align: center;
        margin:10rem 2rem;
        background: rgba(255, 255, 255, 0.459);
        border-radius: 10px;
    }
}





@media only screen and (min-width: 1200px){ /*For big screens*/
    .app{
        width: 80%;
        margin: 2rem auto 0 auto;
        background-color: #FFFFFF;
        border-radius: 10px;
        box-shadow: 0px 5px 20px 5px #000000;
    }
    .header{
        box-shadow: 0px 3px 7px rgba(0,0,0,0.1);
    }
    .header-image{
        height:5rem;
        padding-left:2rem;
    }
    .hero-images{
    display:flex; 
    justify-content: center;
    }
    .hero-images-column-1,.hero-images-column-2,.hero-images-column-3,.hero-images-column-4,.hero-images-column-5{
        width: 15%;
    }
    .image{
        width: 10rem;
        height: 12rem;
        margin: 0.5rem;
        border-radius: 10px;
    }
    .image-additional{
        height: 9rem;
    }
    .hero-images-column-1{
        margin-top: 8rem;
    }
    .hero-images-column-2{
        margin-top: 3rem;
    }
    .hero-images-column-3{
        margin-top: 10rem;
    }
    .hero-images-column-4{
        margin-top: 5rem;
    }
    .hero-images-column-5{
        margin-top: 9rem;
    }
    .hero-text{
        margin-left: 2rem;
        width:40%;
    }
    .hero-text-main{
        font-size: xxx-large;
        font-weight: 600;
    }
    .hero-text-description{
        font-size: x-large;
        font-weight: 300;
    }
}